<template><section>
<h1>Message 消息提示</h1>
<p>常用于主动操作后的反馈提示。与 Notification 的区别是后者更多用于系统级通知的被动提醒。</p>
<h3>基础用法</h3>
<p>从顶部出现，3 秒后自动消失。</p>
<div class="demo-block show-button">
    <o-button @click="open">打开消息提示</o-button>
</div>
<demo-block>
                        <div slot="desc"><o-button @click="open">打开消息提示</o-button>

</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">o-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"open"</span>&gt;</span>打开消息提示<span class="hljs-tag">&lt;/<span class="hljs-name">o-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">var</span> iconList = <span class="hljs-built_in">require</span>(<span class="hljs-string">'../icon.json'</span>)
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
      open() {
        <span class="hljs-keyword">this</span>.$message(<span class="hljs-string">'这是一条消息提示'</span>)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>不同状态</h3>
<p>用来显示「成功、警告、消息、错误」类的操作反馈。</p>
<div class="demo-block show-button">
    <o-button @click="open1">成功</o-button>
    <o-button @click="open2">警告</o-button>
    <o-button @click="open3">消息</o-button>
    <o-button @click="open4">错误</o-button>
</div>
<demo-block>
                        <div slot="desc"><o-button @click="open1">成功</o-button>
<o-button @click="open2">警告</o-button>
<o-button @click="open3">消息</o-button>
<o-button @click="open4">错误</o-button>

</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">o-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"open1"</span>&gt;</span>成功<span class="hljs-tag">&lt;/<span class="hljs-name">o-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">o-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"open2"</span>&gt;</span>警告<span class="hljs-tag">&lt;/<span class="hljs-name">o-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">o-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"open3"</span>&gt;</span>消息<span class="hljs-tag">&lt;/<span class="hljs-name">o-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">o-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"open4"</span>&gt;</span>错误<span class="hljs-tag">&lt;/<span class="hljs-name">o-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">var</span> iconList = <span class="hljs-built_in">require</span>(<span class="hljs-string">'../icon.json'</span>)
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
      open1() {
        <span class="hljs-keyword">this</span>.$message.success(<span class="hljs-string">'恭喜你，这是一条成功消息'</span>)
      },
      open2() {
        <span class="hljs-keyword">this</span>.$message({
          <span class="hljs-attr">message</span>: <span class="hljs-string">'警告哦，这是一条警告消息'</span>,
          <span class="hljs-attr">type</span>: <span class="hljs-string">'warning'</span>
        })
      },
      open3() {
        <span class="hljs-keyword">this</span>.$message({
          <span class="hljs-attr">message</span>: <span class="hljs-string">'这是一条警告消息'</span>,
          <span class="hljs-attr">type</span>: <span class="hljs-string">'info'</span>
        })
      },
      open4() {
        <span class="hljs-keyword">this</span>.$message({
          <span class="hljs-attr">message</span>: <span class="hljs-string">'错了哦，这是一条错误消息'</span>,
          <span class="hljs-attr">type</span>: <span class="hljs-string">'error'</span>
        })
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>Options</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>message</td>
<td>消息文字</td>
<td>string</td>
<td>-</td>
<td>—</td>
</tr>
<tr>
<td>type</td>
<td>主题</td>
<td>string</td>
<td>success/warning/info/error</td>
<td>info</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  var iconList = require('../icon.json');
  export default {
    methods: {
      open() {
        this.$message('这是一条消息提示');
      },
      open1() {
        this.$message.success('恭喜你，这是一条成功消息')
      },
      open2() {
        this.$message({
          message: '警告哦，这是一条警告消息',
          type: 'warning'
        });
      },
      open3() {
        this.$message({
          message: '这是一条警告消息',
          type: 'info'
        });
      },
      open4() {
        this.$message({
          message: '错了哦，这是一条错误消息',
          type: 'error'
        });
      }
    }
  }
</script>


